body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-image: linear-gradient(#292B3F 65%, #323246);
}
body.overlay {
  overflow: hidden;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.admin-html {
  height: 100%;
}

.container {
  width: 90%;
  margin: 5rem auto;
}
@media (min-width: 950px) {
  .container {
    width: 70%;
  }
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Roboto", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
  color: #FFFFFF;
}

h1 {
  width: 75%;
  border-radius: 2rem;
  margin: auto;
  font-size: 2.8rem;
  line-height: 1.4;
}
@media (min-width: 950px) {
  h1 {
    font-size: 3.6rem;
  }
}
@media (min-width: 1440px) {
  h1 {
    font-size: 3.8rem;
  }
}

h2 {
  font-size: 2.6rem;
}
@media (min-width: 950px) {
  h2 {
    font-size: 3.2rem;
  }
}
@media (min-width: 1440px) {
  h2 {
    font-size: 3.4rem;
  }
}

h3 {
  font-size: 2rem;
}
@media (min-width: 950px) {
  h3 {
    font-size: 2.8rem;
  }
}
@media (min-width: 1440px) {
  h3 {
    font-size: 3rem;
  }
}

h4 {
  font-size: 1.8rem;
}
@media (min-width: 950px) {
  h4 {
    font-size: 2.4rem;
  }
}
@media (min-width: 1440px) {
  h4 {
    font-size: 2.6rem;
  }
}

p {
  font-size: 1.6rem;
}
@media (min-width: 950px) {
  p {
    font-size: 2rem;
  }
}

.no-display {
  display: none !important;
}

.light-bg {
  background-color: #323246;
  margin-top: 4rem;
  padding: 2rem;
  border-radius: 2rem;
}

.div-bg {
  background-color: #323246;
  margin-top: 4rem;
  padding: 2rem;
}

.text-left {
  text-align: left;
  background-color: transparent;
  margin: 0;
  margin-bottom: 4rem;
}

.botones {
  background-color: #FD9526;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  border-radius: 1rem;
}
.botones:hover {
  background-color: #fd8403;
  transform: scale(1.03);
  transition: transform 0.2s;
  cursor: pointer;
}

.btn-signup {
  background-color: #292B3F;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.btn-signup:hover {
  background-color: #1b1c29;
  transform: scale(1.03);
  transition: transform 0.2s;
  cursor: pointer;
}
.btn-signup:hover {
  background-color: #FD9526;
}

.btn-compra {
  background-color: #36DE8C;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  border-radius: 1rem;
}
.btn-compra:hover {
  background-color: #22ce7a;
  transform: scale(1.03);
  transition: transform 0.2s;
  cursor: pointer;
}

.slider-main {
  margin-top: 4rem;
  position: relative;
}
.slider-main::after {
  clear: both;
  display: table;
  content: "";
}

.wrapper {
  display: flex;
  gap: 2rem;
  align-items: center;
  overflow-x: hidden;
}
.wrapper .main-container {
  background-color: #323246;
  padding: 2rem;
  border-radius: 2rem;
}

.buttons {
  position: absolute;
  width: 100%;
  top: 45%;
  left: 0;
  z-index: 1;
  display: flex;
}
.buttons .slider-next {
  box-shadow: -2px -2px 5px #202020;
  margin-right: -3.5rem;
  z-index: 1;
  right: 0;
}
.buttons .slider-prev {
  box-shadow: 2px 2px 5px #202020;
  margin-left: -3.5rem;
  z-index: 1;
  left: 0;
}
.buttons .slider-next, .buttons .slider-prev {
  border: none;
  background-color: rgba(255, 255, 255, 0.3);
  color: #292B3F;
  font-size: 3rem;
  font-weight: 700;
  padding: 2.5rem;
  display: grid;
  place-items: center;
  border-radius: 50%;
  position: absolute;
}
.buttons .slider-next:hover, .buttons .slider-prev:hover {
  cursor: pointer;
}

.recuadro {
  width: 70%;
  margin: 5rem auto;
  display: grid;
  place-items: center;
  background-color: #323246;
  padding: 5rem;
  border-radius: 1rem;
}
.recuadro form {
  width: 70%;
  display: flex;
  flex-direction: column;
}
.recuadro form .campos {
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
}
.recuadro form label {
  color: #FFFFFF;
  font-size: 2.4rem;
}
.recuadro form input, .recuadro form textarea {
  background-color: #7E7B99;
  border: none;
  border-radius: 1rem;
  padding: 1rem 1.5rem;
  color: #FFFFFF;
}
.recuadro form input:focus, .recuadro form textarea:focus {
  border: none;
  color: #FFFFFF;
  outline: none !important;
}
.recuadro form textarea {
  height: 12rem;
}
.recuadro form .radio {
  margin: 1rem 0;
  padding: 1rem;
  border-radius: 1rem;
  background-color: #505070;
}
.recuadro form .radio label {
  margin-right: 3rem;
}
.recuadro form .radio .label {
  display: block;
  position: relative;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  /* Show the indicator (dot/circle) when checked */
}
.recuadro form .radio .label input[type=radio] {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}
.recuadro form .radio .label .checkmark {
  position: absolute;
  top: 15%;
  right: 0;
  height: 25px;
  width: 25px;
  background-color: #eee;
}
.recuadro form .radio .label input[type=radio]:checked ~ .checkmark {
  background-color: #FD9526;
}
.recuadro form .radio .label .checkmark:after {
  content: "";
  position: absolute;
  display: none;
}
.recuadro form .radio .label input[type=radio]:checked ~ .checkmark:after {
  display: block;
}
.recuadro form .radio .label .checkmark:after {
  top: 9px;
  left: 9px;
  width: 8px;
  height: 8px;
  background: white;
  border-radius: 50%;
}
.recuadro form .rememberMe {
  display: flex;
  font-size: 2rem;
}
.recuadro form .rememberMe span {
  margin-left: 1rem;
}
.recuadro form .forgot {
  display: flex;
  flex-direction: column;
}
.recuadro form .forgot a {
  margin-bottom: 2rem;
}

.bloque-btn {
  display: flex;
  flex-direction: column;
}
.bloque-btn a {
  margin-bottom: 2rem;
}

.disabled {
  pointer-events: none;
}

.btn-search {
  position: absolute;
  top: 50%;
  right: -100%;
  transform: translate(-50%, -50%);
  width: 1.7rem;
}
@media (min-width: 950px) {
  .btn-search {
    width: 3rem;
    right: 25%;
  }
}
@media (min-width: 1250px) {
  .btn-search {
    right: 40%;
  }
}

.btn-menu {
  filter: sepia(100%) grayscale(1) brightness(1);
}
.btn-menu.active {
  filter: none;
}
.btn-menu:hover {
  filter: none;
}

.btn-dropdown {
  cursor: pointer;
}

.btn-submit, .btn-submit--extra {
  background-color: #FD9526;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  width: fit-content;
  border-radius: 1rem;
  color: #292B3F;
}
.btn-submit:hover, .btn-submit--extra:hover {
  background-color: #fd8403;
  transform: scale(1.03);
  transition: transform 0.2s;
  cursor: pointer;
}
.btn-submit--extra {
  margin: 2.5rem auto 0;
  font-size: 1.6rem;
}

.btn-view {
  background-color: #FD9526;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: flex;
  border: none;
  margin-top: 1rem;
  border-radius: 1rem;
  color: #292B3F;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}
.btn-view:hover {
  background-color: #fd8403;
  transform: scale(1.03);
  transition: transform 0.2s;
  cursor: pointer;
}

.btn-delete {
  background-color: #CF2020;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  margin-top: 1rem;
  width: fit-content;
  border-radius: 1rem;
  color: #FFFFFF;
}
.btn-delete:hover {
  background-color: #b01b1b;
  transform: scale(1.03);
  transition: transform 0.2s;
  cursor: pointer;
}

.btn-update {
  background-color: #1ba360;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: flex;
  border: none;
  margin-top: 1rem;
  border-radius: 1rem;
  color: #FFFFFF;
}
.btn-update:hover {
  background-color: #16844e;
  transform: scale(1.03);
  transition: transform 0.2s;
  cursor: pointer;
}

.header__bar {
  margin: 1.2rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
@media (min-width: 380px) {
  .header__bar {
    margin: 2.5rem;
  }
}
@media (min-width: 950px) {
  .header__bar {
    margin: 2.5rem 5rem;
    grid-template-columns: 3fr 1fr;
    align-items: center;
  }
}
.header__left {
  display: flex;
  flex-direction: column;
  row-gap: 1rem;
  align-items: flex-start;
}
@media (min-width: 950px) {
  .header__left {
    flex-direction: row;
    align-items: center;
    row-gap: 0;
    column-gap: 2rem;
  }
}
.header__left--logo {
  width: 12rem;
  margin-top: 2rem;
}
@media (min-width: 380px) {
  .header__left--logo {
    width: 16rem;
  }
}
@media (min-width: 950px) {
  .header__left--logo {
    width: 20rem;
    margin-right: 3rem;
    margin-top: 0;
  }
}
.header__left--arrow {
  color: #505070;
  font-size: 4rem;
}
.header__left__search-bar {
  background-color: #505070;
  width: 200%;
  padding: 0.7rem;
  border-radius: 2rem;
  display: flex;
  align-items: center;
  border: none;
  font-size: 1.4rem;
}
@media (min-width: 950px) {
  .header__left__search-bar {
    width: 75%;
    padding: 1.2rem;
    font-size: 1.6rem;
  }
}
@media (min-width: 1250px) {
  .header__left__search-bar {
    width: 60%;
  }
}
.header__left__search-bar:focus {
  outline: 2px solid #FD9526;
  caret-color: #FD9526;
}
.header__left__label {
  width: 100%;
  position: relative;
  margin-top: 3rem;
}
@media (min-width: 950px) {
  .header__left__label {
    margin-top: 0;
  }
}

.header__login {
  display: flex;
  margin: 0;
  align-items: flex-end;
  justify-content: flex-start;
  height: 100%;
  margin: 0.5rem 0;
  flex-direction: column;
  gap: 1rem;
}
@media (min-width: 380px) {
  .header__login {
    gap: 2rem;
  }
}
@media (min-width: 950px) {
  .header__login {
    width: 100%;
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-end;
    align-items: center;
    margin: auto;
    gap: 0;
  }
}
.header__login__btn, .header__login__btn--signup, .header__login__btn--login {
  color: #FFFFFF;
  padding: 0.5rem 1.5rem;
  border-radius: 1rem;
  font-family: "Lato", sans-serif;
  font-weight: 700;
  font-size: 1.4rem;
  line-height: 1.2;
  width: 75%;
}
.header__login__btn:hover, .header__login__btn--signup:hover, .header__login__btn--login:hover {
  transform: scale(1.05);
}
@media (min-width: 380px) {
  .header__login__btn, .header__login__btn--signup, .header__login__btn--login {
    font-size: 1.7rem;
    padding: 0.3rem 2rem;
  }
}
@media (min-width: 950px) {
  .header__login__btn, .header__login__btn--signup, .header__login__btn--login {
    width: auto;
    padding: 0.5rem 3rem;
  }
}
.header__login__btn--login {
  background-color: #505070;
  text-decoration: underline;
  border: none;
  cursor: pointer;
}
.header__login__btn--signup {
  background-color: #FD9526;
  margin-left: -0.5rem;
}
@media (min-width: 380px) {
  .header__login__btn--signup {
    margin-left: -1rem;
  }
}

.footer {
  background-color: #292B3F;
  padding: 5rem 1rem;
  place-items: center;
  margin-top: 2rem;
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 950px) {
  .footer {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.footer__social {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-bottom: 2rem;
}
@media (min-width: 950px) {
  .footer__social {
    padding-bottom: 0;
  }
}
.footer__social--logo {
  width: 20rem;
}
@media (min-width: 380px) {
  .footer__social--logo {
    width: 24rem;
  }
}
@media (min-width: 950px) {
  .footer__social--logo {
    width: 30rem;
  }
}
.footer__social__links {
  margin-top: 2.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 2.5rem;
}
.footer__social__links i {
  font-size: 3.2rem;
  color: #FD9526;
}
.footer__social--copyright {
  color: #FD9526;
}
.footer__contact {
  display: none;
}
@media (min-width: 950px) {
  .footer__contact {
    display: block;
    text-align: center;
  }
  .footer__contact h3, .footer__contact p {
    color: #FD9526;
  }
  .footer__contact h3 {
    font-weight: 700;
  }
}
.footer .footer__terms {
  display: none;
}
@media (min-width: 950px) {
  .footer .footer__terms {
    display: flex;
    flex-direction: column;
    text-align: left;
