.alerta {
  padding: 1.5rem 1.5rem 1.5rem 3rem;
  text-transform: uppercase;
  margin: 1rem 0;
  font-size: 1.4rem;
  font-weight: 700;
  border-radius: 1rem;
}
.alerta__error {
  background-color: white;
  border-left: 1rem solid #CF2020;
  color: #CF2020;
}
.alerta__exito {
  background-color: #e5fbf0;
  border-left: 1rem solid #36DE8C;
  color: #15804c;
}

.modal-alerta {
  background-color: #323246;
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
  border-radius: 1rem;
  width: auto;
  height: auto;
  padding: 3rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 11;
}
.modal-alerta__icono {
  font-size: 8rem;
  color: #FD9526;
  animation: transform 3s ease-in-out infinite;
}
@keyframes transform {
  0% {
    transform: rotateY(360deg);
  }
  50% {
    transform: scale(0deg);
  }
  100% {
    transform: scale(360deg);
  }
}
.modal-alerta__titulo {
  margin: 1rem auto;
  font-weight: 400;
}
.modal-alerta__parrafo {
  margin: 0;
}
