  background-color: #29CBFC;
  color: #FFFFFF;
}
.modal-alerta--activo {
  background-color: rgba(0, 0, 0, 0.7);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

